import Header from "./Components/Header.js"
import Items from "./Components/Items.js"
import {useState,useEffect} from "react"
import {BrowserRouter as Router} from "react-router-dom"
import {CSSTransition} from "react-transition-group"


const App = () =>{
  const[itemList,setItemList] = useState([
    {
      name:"Shampoo",
      price:5.5,
      img:"0003"
    },{
      name:"Soap",
      price:7.5,
      img:"0002"
    },{
      name:"Hair Conditioner",
      price:9.5,
      img:"0001"
    },{
      name:"Baby Powder",
      price:2.5,
      img:"0004"
    }
  ])
  const[cartList,setCartList]=useState([])
  const[menu,Setmenu]=useState("main");
  const [openCart,setOpenCart]=useState(false);
  const [menuHeight,setMenuHeight]=useState(100);
  const [total,setTotal] = useState(0);
  useEffect(()=>{
    setTotal(0);
    for(let cart of cartList){
        setTotal(total+cart.price);
    }
  },[cartList]);
  const onSub = (item)=>{
    for(let cart of cartList){
      if(cart.name==item.name){
        return;
      }
    }
    setCartList([...cartList,item])
    calcHeight(document.getElementsByClassName("dropdown"));
}
const handleSubmit = (e) => {
  e.preventDefault()
  let myForm = document.getElementById('pizzaOrder');
  let formData = new FormData(myForm)
  fetch('/', {
    method: 'POST',
    headers: { "Content-Type": "application/x-www-form-urlencoded" },
    body: new URLSearchParams(formData).toString()
  }).then(() => console.log('Form successfully submitted')).catch((error) =>
    alert(error))
}
const calcHeight=(el)=>{
  const height = el.offsetHeight+32;
  setMenuHeight(height)
}
  return (
    <Router>
      <Header cartState={openCart} setCartState={setOpenCart} cartList={cartList} Setmenu={Setmenu} menu={menu}
      menuHeight={menuHeight} setMenuHeight={setMenuHeight} calcHeight={calcHeight} total={total}/>
      <Items itemLists={itemList} onSubmit={onSub} calcHeight={calcHeight}/>
    </Router>
  );
}

export default App;
