import { Link } from "react-router-dom"
import {CSSTransition} from "react-transition-group"
import CartItem from "./CartItem.js"
const CartDropDown = ({cartList,Setmenu,menu,menuHeight,setMenuHeight,calcHeight,total}) => {
  
    return (
    <div  className="dropdown" style = {{height:menuHeight}}>
        <div className="relate">
            <CSSTransition in={menu ==="main"} unmountOnExit timeout={500} classNames = "menu-primary" onEnter={calcHeight}>
                <div class="menu">
                {
                    cartList.map( (cartItem)=>{
                    return <CartItem item={cartItem} total={total} />
                    })
                }
                <p>Total price is {total}</p>
                <Link to ="#" onClick={()=>{Setmenu("pay")}}>Pay</Link>
                </div>
            </CSSTransition>
            <CSSTransition in={menu ==="pay"} unmountOnExit timeout={500} classNames = "menu-secondary" onEnter={calcHeight}>
            <form class="menu" data-netlify="true">
                <div className="flex">
                    <p>Total price is {total}</p>
                    <input name="submit" type="submit" value="submit"/>
                    <input type="text" hidden name="total" value={total}/>
                </div>
                <Link to ="#" onClick={()=>{Setmenu("main")}}>Go back</Link>
            </form>
            </CSSTransition>
        </div>
    </div>
    )
}

export default CartDropDown
