import {Link} from "react-router-dom"
import Cart from "./Cart.js"
import {ReactComponent as Icon} from "../img/shopping-cart.svg"
import CartDropDown from "./CartDropDown.js"

const Header = ({cartState,setCartState,cartList,Setmenu,menu,menuHeight,setMenuHeight,calcHeight,total}) => {
    return (
         <div className = "Header container flex">
                <h1>Shopeefy</h1>
                <ul className="flex">
                    <Cart icon ={<Icon/>} cartState={cartState} setCartState={setCartState} cartList={cartList}>
                        <CartDropDown cartList={cartList} Setmenu={Setmenu} menu={menu} menuHeight={menuHeight} setMenuHeight={setMenuHeight} calcHeight={calcHeight}
                        total={total} />
                    </Cart>
                </ul>
            </div>
     
    )
}

export default Header
