import conditioner from "../img/conditioner.png"
import powder from "../img/powder.jpg"
import shampoo from "../img/shampoo.jpg"
import soap from "../img/soap.png"
import CartDropDown from "./CartDropDown.js"


const Item = ({item,onSub,parent}) => {
    const image = {
        "0001":conditioner,
        "0002":powder,
        "0003":shampoo,
        "0004":soap,
    }
    
    return (
        <form className='flex Item' onSubmit={(e)=>{
            e.preventDefault()
            onSub(item)
        }}>
            <h2>{item.name}</h2>
            <img src ={image[item.img]}>  
            </img>
            <p>Price:{item.price}</p>
            <input type="submit" value="Add To Cart"/>
        </form>
    )
}

export default Item
