import {Link} from "react-router-dom"


const Cart = ({icon,cartState,setCartState,children,cartList}) => {
    return (
        <>
          <li className="flex cart-item">
            <Link to = "#" className="icon-button flex" onClick={()=>{
              setCartState(!cartState);
            }}>{icon}
            </Link>
            {(()=>{
              if(cartState&&cartList.length>0){
                return children;
              }
            })()}
          </li>  
        </>
    )
}

export default Cart
