import {useState} from "react"
const CartItem = ({item,total}) => {
   const[cost,setCost]= useState(item.price);
   const onChange = (e)=>{
       setCost(e.target.value*item.price);
   }
   
    return (
        <div className="cart-comp">
            <p>{item.name}</p>
            <input type="number" min="1" max="9" onChange={onChange} defaultValue="1"></input>
            <p>{item.price}</p>
            <p>{cost}</p>
        </div>
    )
}

export default CartItem
